import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow from './CardRow'

//export default function OrganAutoCard(props) {
const OrganAutoCard = React.memo((props) => {
  console.log(`! render OrganAutoCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}></span>;

  const organAuto = props.organAuto;
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        <CardRow descr="" value={organAuto?.Name} rightWidth={'50%'}></CardRow>
        <CardRow descr="" value={organAuto?.Projection}></CardRow>
        <CardRow descr="" value={organAuto?.Direction}></CardRow>
        <CardRow descr=". " value={ organAuto?.AgeId}></CardRow>
        <CardRow descr="" value={organAuto?.Constitution}></CardRow>
      </CardContent>
    </Card>
  );
});

export default OrganAutoCard;