import React, {useContext}  from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow, {CardErrorRow} from './CardRow'

const GeneratorCard = React.memo((props) => {
//export default function GeneratorCard() {
  console.log(`! render GeneratorCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}></span>;

  const generator = props.generator;
  const heatStatus = generator?.State?.HeatStatus;
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        <CardRow descr="" 
          value={generator?.State?.State > 0? "" : " "}
          rightColor={generator?.State?.State > 0? "green" : "red"}
        ></CardRow>
        <CardRow descr="" value={generator?.State?.Mas ? generator.State.Mas + ' ' : ''}></CardRow>
        <CardRow descr="" value={generator?.State?.Kv ? generator.State.Kv + ' ' : ''}></CardRow>
        <CardRow descr=". . ." value={generator?.State?.Workstation}></CardRow>
        <CardRow descr="" 
          value={heatStatus === 2 ? "" : heatStatus === 3 ? "" : heatStatus === 1 ? "" : ""}
          rightColor={heatStatus === 2 ? "yellow" : heatStatus === 3 ? "red" :  heatStatus === 1 ? "green" : "gray"}
        ></CardRow>
        <CardRow descr="" value={
          generator?.State?.Focus === 1 ? "" : generator?.State?.Focus === 0 ? "" : ""
        }></CardRow>
        <CardRow descr="" 
          value={ generator?.State?.PedalPressed === 1 ? "" : generator?.State?.PedalPressed === 2 ? "" : " "} 
          rightColor={generator?.State?.PedalPressed === 1 || generator?.State?.PedalPressed === 2? "green" : undefined }
        ></CardRow>
        <CardRow descr="" value={generator?.State?.ErrorDescriptions?.length}></CardRow>
        {generator?.State?.ErrorDescriptions?.length ? 
          generator.State.ErrorDescriptions.map((i, ind) => (
            <CardErrorRow key={ind.toString()}  descr={i.Code} value={i.Description} ></CardErrorRow>
            ))
            :
            <></>          
        }
      </CardContent>
    </Card>
  );
});

export default GeneratorCard;
